.top_vintage_row {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    align-content: center;
    
    &>* {
            &:first-child {
                width: 630px;
                background-color: #fafafa;
            }
    
            &:last-child {
                width: 630px;
                background: #EAE9E7;
            }
        }

}

.top_vintage_button {
    padding: 10px;
    position: relative;
}

.top_vintage_card {
    display: flex;
    align-items: center;
    gap: 10px;

   
}