.top_shopping_row {
    margin-top: 30px;
    display: flex;
    gap: 40px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 295px;

}

.shoping_images_5 {
    position: relative;
    object-fit: contain;
}

.shoping_images_2 {
    width: 295px;
    height: 466px;
    display: block;
}

.shoping_images_3 {
    width: 295px;
    height: 400px;
}

.shoping_images {
    display: flex;
    align-items: center;
    height: 400px;
    width: 295px;
    align-content: center;
}

.Hover__card {
    width: 306px;
    height: 555px;
    overflow: hidden;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
}

.Hover__card:hover {
    .Hover__card__bottom {
        transform: translateY(-575px);
        &.b1{
            transform: translateY(-594px);
        }
    }
}

.Hover__card__bottom {
    background-color: rgba(0, 0, 0, 0.4);
    width: 306px;
    height: 555px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 25px;
    position: relative;
    transition: .5s ease;
    top: 54px;
    padding: 30px;
color: #fff;
    .btn {
        width: 120px;
        height: 40px;
        margin-bottom: 90px;
        background: transparent;
        border: 2px solid #fff;
        color: #fff;
    }

}

