}

.card_pp_pr {
    display: flex;
    width: 120px;
    margin-right: 50px;
    gap: 50px;

}

.products_card_title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 20px;
}

.products_card {
    width: 750px;

}

.products_imagess {
    width: 240px;
