.Home_baddshet_row {
    margin-top: 3rem;

    .baddshet {
        width: 1302px;
        height: 400px;
       background-image: url("../../images/bagroundimge.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: relative;

        .beddshet_content {
            position: absolute;
            top: 89px;
            left: 147px;
            width: 300px;
            height: auto;
            padding: 2rem 4rem;
            background: rgba(255, 255, 255, 0.80);

            h1 {
                font-size: 25px;
            }

            .baddshet_price {
                height: 20px;
                display: flex;
                justify-content: start;
                align-items: center;
                gap: 20px;
                padding-top: 10px;


                h3 {
                    font-size: 28px;
                    color: rgb(139, 91, 36);
                }

                h4 {
                    font-size: 18px;
                    text-decoration-line: line-through;
                    color: rgb(139, 91, 36);
                }
            }

            .bedsheet-title {
                padding: 16px 0;
            }
        }
    }
}